import React, {useState} from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Header from './components/Header';
import {ApolloProvider, ApolloClient, InMemoryCache} from '@apollo/client'
import Sidebar from './components/Sidebar';
import Home from './pages/Home';
import NotFound from './pages/NotFound';
import Task from './pages/Task'
import Auth from './components/Auth';
import AdminDashboard from './pages/AdminDashboard';
import UserDashboard from './pages/UserDashboard'
import TeamMembers from './pages/TeamMembers';
import UserTeamMembers from './pages/UserTeamMembers';
import AllUsers from './components/AllUsers';
import Unauthorized from './pages/Unauthorized';
import AssignedToYou from './pages/AssignedToYou';
import Profile from './pages/Profile'
import AdminStatistics from './pages/AdminStatistics';
import UserStatistics from './pages/UserStatistics';

const cache = new InMemoryCache({
  typePolicies: {
    Query: {
      fields: {
        teamMembers: {
          merge(existing, incoming){
            return incoming;
          },
          
        },
        users: {
          merge(existing, incoming){
            return incoming;
          },
          
        },
        tasks: {
          merge(existing, incoming){
            return incoming;
          },
          
        },
         task: {
          merge(existing, incoming){
            return incoming;
          },
          
        }


      }
    }
  }
})

const client = new ApolloClient({
  uri: '/graphql',
  cache,
})

function App() {
  const[showSidebar, setShowSidebar] = useState(true)
  const[user, setUser] = useState(localStorage.getItem('user'))
  const checkAdmin = user?user.role:'';
  const isAdmin = checkAdmin ==='authorizedAdmin'
  return (
  <ApolloProvider client={client}>
    <div className='wrapper'>
  
  <BrowserRouter>
    <Sidebar showSidebar={showSidebar} setShowSidebar={setShowSidebar} user={user}/>
    <div id='content'>
    <Header user={user} setUser={setUser} showSidebar={showSidebar} setShowSidebar={setShowSidebar} />
    <Routes>
      <Route path='/' element={<Home/>}/>
      <Route path='/adminDashboard' element={isAdmin?<AdminDashboard user={user}/>:<Unauthorized/>}/>
      <Route path='/userDashboard' element={<UserDashboard user={user} />}/>
      <Route path='auth' element={<Auth user={user} setUser= {setUser} />}/>
      <Route path='allUsers' element={<AllUsers/>}/>
      <Route path='allTeamMembers' element={<TeamMembers user={user}/>}/>
      <Route path='userTeamMembers' element={<UserTeamMembers user={user}/>}/>
      <Route path='tasks/:id' element={<Task user={user}/>}/>
      <Route path='assignedToYou' element={<AssignedToYou user={user}/>} />
      <Route path='profile' element={<Profile user={user} setUser={setUser}/>} />
      <Route path='allStats' element={<AdminStatistics/>} />
      <Route path='userStats' element={<UserStatistics user={user}/>} />
      
      <Route path='*' element={<NotFound user={user} setUser={setUser}/>}/>
  </Routes>
    </div>
    
  </BrowserRouter>  
    </div>
    </ApolloProvider>
  );
}

export default App;
