import React from 'react'
import axios from 'axios';
import {FaTrash} from "react-icons/fa"
import {DELETE_USER} from '../mutations/user';
import { useMutation } from '@apollo/client';
import { FETCH_USERS } from '../queries/user';

const UserRow = ({user}) => {
  const [deleteUser] = useMutation(DELETE_USER, {
    variables: {id: user.id},
    // refetchQueries: [{query: GET_TEAM_MEMBERS}]
    update(cache, {data: { deleteUser}}) {
      const {users} = cache.readQuery({query: FETCH_USERS});
      cache.writeQuery({
        query: FETCH_USERS,
        data: {users: users.filter(user => user.id!== deleteUser.id)}
      })
    }
  })
   const deleteUserImage = async (file) =>{
  try {
    
    if(!file) {return}
    else{
      const {data}= await axios.delete(`/delete/${file}`)
      console.log(data);
    }
  } catch (error) {
    console.log(error);
  }
 }

 const handleDelete = () =>{
  deleteUser(user.id)
  deleteUserImage(user.fileName)
 }
  
  return (
    <tr>
      <td>{user.name}</td>
      <td>{user.email}</td>
      <td>{user.role}</td>
      <td>
       <button className='btn btn-danger btn-sm' onClick={handleDelete}>
        <FaTrash/>
       </button>
      </td>
    </tr>
  )
}

export default UserRow
