import React from 'react'
import {useNavigate} from 'react-router-dom';
import image from '../images/hero1.jpg'
import './Home.css'
const Home = () => {
 const navigate = useNavigate();
  return (
   
        <div className='content-container'>
          <div className='desc-container'>
            <div className='logoDiv'>
              <h2 className='logo'><span className='logoSpan'>Easy</span> Tasking</h2>
            </div>
            <div className='heroDiv'>
              <h1 className='hero'><span className='heroSpan'>Task</span> Managing <span className='heroSpan'>App</span></h1>
            </div>
            <div className='descriptionDiv'>
              <p className='description'>
               An excellent app for creating, updating and managing the tasks for your crew in a ship building environment. It can also be easily adopted as an issue tracker or note making app. Enjoy using it.
              </p>
            </div>
            <div className='action'>
              <button variant='contained' className='btn btn-secondary mt-3' onClick={()=>navigate('/auth')}>Login / Register</button>
            </div>
          </div>
          <div className='imageContainer'>
            <div >
              <img  src= {image} alt='transmission' className='image'></img>
            </div>
          </div>
        </div>
  );
};

export default Home

