import React from 'react'
import {FaEnvelope, FaPhone, FaIdBadge, } from 'react-icons/fa'
import {AiFillInteraction} from 'react-icons/ai'

const TeamMemberInfo = ({teamMember}) => {
  return (
    <>
      <h5 className="mt-5">Assigned To:</h5>
      <ul className="list-group">
       <li className="list-group-item">
        <FaIdBadge className='icon'/>{teamMember?teamMember.name:''}
       </li>
       <li className="list-group-item">
        <FaEnvelope className='icon'/>{teamMember?teamMember.email:''}
       </li>
       <li className="list-group-item">
        <FaPhone className='icon'/>{teamMember?teamMember.phone:''}
       </li>
       <li className="list-group-item">
        <AiFillInteraction className='icon'/>{teamMember?teamMember.role:''}
       </li>

      </ul>
    </>
  )
}

export default TeamMemberInfo
