import React, {useState} from 'react'
import { useQuery } from '@apollo/client'
import {FETCH_USERS} from '../queries/user'
import Spinner from './Spinner'
import UserRow from './UserRow'
import ReactPaginate from 'react-paginate'
import { Link } from 'react-router-dom'

const AllUsers = () => {
 const[pageNumber, setPageNumber]=useState(0)

 const {loading, error, data} = useQuery(FETCH_USERS)


 if(loading) return <Spinner/>
 if(error) return <h3>Something Went Wrong!</h3>

  const users= data?data.users:[]
  const usersPerPage = 8;
 const pagesVisited = pageNumber * usersPerPage;
 const pageCount = users?Math.ceil(users.length/usersPerPage):0;
 
 const displayUsers = users?users.slice(pagesVisited, pagesVisited + usersPerPage):[];
  const changePage = ({selected}) => {
  setPageNumber(selected)
  }


  return (
    <>
     {!loading && !error && (
        <table className='table table-hover mt-3'>
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Role</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {displayUsers.map((user) => {
              return <UserRow key={user.id} user={user} />
            }
            )}
          </tbody>
        </table>
      )}
        <ReactPaginate
         previousLabel = "Prev"
         nextLabel = "Next"
         pageCount={pageCount}
         onPageChange={changePage}
         containerClassName= {'paginationButtons'}
         previousLinkClassName={'previous'}
         nextLinkClassName={'next'}
         activeClassName={'active'}
         />
        <button className='btn bt-secondary'><Link to='/adminDashboard'>Back To Tasks</Link></button>
    </>
  )
}

export default AllUsers
