import React, {useState} from 'react'
import { useQuery } from '@apollo/client'
import Spinner from './Spinner'
import TaskCard from './TaskCard'
import { GET_TASKS } from '../queries/taskQueries'
import ReactPaginate from 'react-paginate';
import BarChart from './BarChart'
const UserTasks = ({user,}) => {
const[pageNumber, setPageNumber]=useState(0)
// const[userChartData, setUserChartData] = useState()


const {loading, error, data} = useQuery(GET_TASKS);
 if(loading) return <Spinner/>
 if(error) return <h4>Something went wrong</h4>

 const tasks = data? data.tasks: [];
 const userId = user?user.id: '';
  
 const userTasks = tasks.filter((task) => task.creatorId === userId);
 const tasksPerPage = 8;
 const pagesVisited = pageNumber * tasksPerPage;
 const pageCount = Math.ceil(userTasks.length/tasksPerPage)
 
 const displayTasks = userTasks.slice(pagesVisited, pagesVisited + tasksPerPage)

 const changePage = ({selected}) => {
  setPageNumber(selected)
  
 }

 const pendingPriorityOne = userTasks.filter(task=>task.priority ==='#1' && task.status === 'Pending').length
 const pendingPriorityTwo = userTasks.filter(task=>task.priority ==='#2' && task.status === 'Pending').length
 const pendingPriorityThree = userTasks.filter(task=>task.priority ==='#3' && task.status === 'Pending').length


 const completedPriorityOne = userTasks.filter(task=>task.priority ==='#1' && task.status === 'Completed').length
 const completedPriorityTwo = userTasks.filter(task=>task.priority ==='#2' && task.status === 'Completed').length
 const completedPriorityThree = userTasks.filter(task=>task.priority ==='#3' && task.status === 'Completed').length




    return (
      <>
    <div className="d-flex justify-content-center">
          <h3 className='text-danger '>Your Tasks</h3>
    </div>
      {userTasks.length>0?(<div className='row mt-4'>
        <div className="container d-flex justify-content-center mb-4 text-danger">
        </div>
       {displayTasks.map(task=>{
        return <TaskCard key={task.id} task={task}/>
       })}
      </div>): (<h4>No Tasks To Display</h4>)}
        <ReactPaginate
         previousLabel = "Prev"
         nextLabel = "Next"
         pageCount={pageCount}
         onPageChange={changePage}
         containerClassName= {'paginationButtons'}
         previousLinkClassName={'previous'}
         nextLinkClassName={'next'}
         activeClassName={'active'}
         />
         <div className='d-flex justify-content-center align-items-center'>
        <BarChart chartData={{labels: ['Priority #1', 'Priority #2', 'Priority #3'],
        datasets:[{ 
          label: 'Pending Tasks',data:[pendingPriorityOne,pendingPriorityTwo, pendingPriorityThree],
          backgroundColor:["cyan",]
        }],
        
      }}/>
              <BarChart chartData={{labels: ['Priority #1', 'Priority #2', 'Priority #3'],
        datasets:[{ 
          label: 'Completed Tasks',
          data:[completedPriorityOne,completedPriorityTwo, completedPriorityThree],
          backgroundColor:["green",]
        }],
        
      }}/>
      </div>

    </>
  )
}

export default UserTasks
