import { gql } from "@apollo/client";


const FETCH_USERS = gql`
query getUsers{
   users{
      id
      name
      email
      role
      token
      tokenExpiration
      fileSystem
   }
}
`

const GET_USER = gql`
query getUser($id: ID!){
   user(id: $id){
      id
      name
      email
      role
      fileSystem
      fileName
      token
      tokenExpiration
      tasks{
         id
         title
         status
         priority
         system
      }
      teamMembers{
         id
         name
         email
         role
         phone
      }
   }
}
`


export {FETCH_USERS, GET_USER};