import React from 'react'
import {FaEnvelope, FaIdBadge, } from 'react-icons/fa'
import {AiFillInteraction} from 'react-icons/ai'

const TeamMemberInfo = ({creator}) => {
  return (
    <>
      <h5 className="mt-5">Created By:</h5>
      <ul className="list-group">
       <li className="list-group-item">
        <FaIdBadge className='icon'/>{creator?creator.name:''}
       </li>
       <li className="list-group-item">
        <FaEnvelope className='icon'/>{creator?creator.email: ''}
       </li>
       <li className="list-group-item">
        <AiFillInteraction className='icon'/>{creator?creator.role: ''}
       </li>

      </ul>
    </>
  )
}

export default TeamMemberInfo
