import React, {useState} from 'react'
import { useQuery } from '@apollo/client'
import TeamMemberRow from '../components/TeamMemberRow'
import { GET_TEAM_MEMBERS } from '../queries/teamMemberQueries'
import Spinner from '../components/Spinner'
import AddTeamMemberModal from '../components/AddTeamMemberModal'
import ReactPaginate from 'react-paginate'
import { Link } from 'react-router-dom'


const TeamMembers = ({user}) => {
  const[pageNumber, setPageNumber]=useState(0)

 const {loading, error, data} = useQuery(GET_TEAM_MEMBERS)
 const teamMembers= data?data.teamMembers:[]
  const teamMembersPerPage = 8;
 const pagesVisited = pageNumber * teamMembersPerPage;
 const pageCount = teamMembers?Math.ceil(teamMembers.length/teamMembersPerPage):0;
 
 const displayTeamMembers = teamMembers?teamMembers.slice(pagesVisited, pagesVisited + teamMembersPerPage):[];

  const changePage = ({selected}) => {
  setPageNumber(selected)
  
 }


 if(loading) return <Spinner/>
 if(error) return <h3>Something Went Wrong!</h3>

  return (
    <>
    <AddTeamMemberModal user={user}/>
            <div className="container d-flex justify-content-center mb-4 text-primary mt-4">
          <h3>All Team Members</h3>
        </div>

     {!loading && !error && (
        <table className='table table-hover mt-3'>
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Phone</th>
              <th>Role</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {displayTeamMembers.map((teamMember) => {
              return <TeamMemberRow key={teamMember.id} teamMember={teamMember} />
            }
            )}
          </tbody>
        </table>
      )}
         <ReactPaginate
         previousLabel = "Prev"
         nextLabel = "Next"
         pageCount={pageCount}
         onPageChange={changePage}
         containerClassName= {'paginationButtons'}
         previousLinkClassName={'previous'}
         nextLinkClassName={'next'}
         activeClassName={'active'}
         />

      <button className='btn bt-secondary'><Link to='/adminDashboard'>Back To Tasks</Link></button>


    </>
  )
}

export default TeamMembers
