import React, {useState,} from 'react'
import { useQuery } from '@apollo/client'
import TeamMemberRow from '../components/TeamMemberRow'
import { GET_TEAM_MEMBERS } from '../queries/teamMemberQueries'
import AddTeamMemberModal from '../components/AddTeamMemberModal'
import ReactPaginate from 'react-paginate'
import { Link } from 'react-router-dom'

const UserTeamMembers = ({user}) => {
const[pageNumber, setPageNumber]=useState(0)

 const {loading, error, data} = useQuery(GET_TEAM_MEMBERS);
 const userId = user? user.id: '';
 const userTeamMembers = data? data.teamMembers.filter(teamMember => teamMember.creatorId === userId): null;

 const teamMembersPerPage = 8;
 const pagesVisited = pageNumber * teamMembersPerPage;
 const pageCount = userTeamMembers?Math.ceil(userTeamMembers.length/teamMembersPerPage):0;
 
 const displayUserTeamMembers = userTeamMembers?userTeamMembers.slice(pagesVisited, pagesVisited + teamMembersPerPage):[];

 const changePage = ({selected}) => {
  setPageNumber(selected)
  
 }

 
 if(loading) return null

 if(error) return <h4>Something went wrong</h4>
  return (
    <>
    <AddTeamMemberModal user={user}/>
            <div className="container d-flex justify-content-center mb-4 text-primary mt-4">
          <h3>Your Team Members</h3>
        </div>

     {userTeamMembers.length?(
        <table className='table table-hover mt-3'>
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Phone</th>
              <th>Role</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {displayUserTeamMembers.map((teamMember) => {
              return <TeamMemberRow key={teamMember.id} teamMember={teamMember} user={user}/>
            }
            )}
          </tbody>
        </table>
        
      ):(<h4>No Members To Display</h4>)}
        <ReactPaginate
         previousLabel = "Prev"
         nextLabel = "Next"
         pageCount={pageCount}
         onPageChange={changePage}
         containerClassName= {'paginationButtons'}
         previousLinkClassName={'previous'}
         nextLinkClassName={'next'}
         activeClassName={'active'}
         />
      <button className='btn bt-secondary'><Link to='/userDashboard'>Back To Tasks</Link></button>
    </>
  )
}

export default UserTeamMembers
