import { gql } from "@apollo/client";

const ADD_USER = gql`
mutation addUser($name: String!, $email: String!, $password: String!, $role: String!, $fileSystem: String, $fileName: String){
 addUser(name: $name, email: $email, password: $password, role: $role, fileSystem: $fileSystem, fileName: $fileName){
    id,
    name,
    email,   
    role,
    token,
    tokenExpiration,
    fileSystem,
 }
}
`
const DELETE_USER = gql`
mutation deleteUser($id: ID!){
   deleteUser(id: $id){
      id
      name
   }
}
`
const UPDATE_USER = gql`
mutation updateUser($id:ID!, $name: String!, $email: String!, $password: String!, $role: String!, $fileSystem: String, $fileName: String){
   updateUser(id:$id, email: $email, name: $name, password: $password, role: $role, fileSystem: $fileSystem, fileName: $fileName){
      id
      name
      email
      role
      fileSystem
      }
}
`


export {ADD_USER, DELETE_USER, UPDATE_USER};