import React from 'react'
import {Link,} from 'react-router-dom'
import Spinner from '../components/Spinner'
import { useQuery,} from '@apollo/client'
import { GET_USER, } from '../queries/user'
import DeleteUserButton from '../components/DeleteUserButton'
import EditProfileForm from '../components/EditProfileForm'

const Profile = ({user, setUser}) => {


 const id = user?user.id:'';
 const{loading, error, data} = useQuery(GET_USER, {
  variables: {
   id: id
  }
 })
 const name = data?data.user.name:''
 const userName = name?name.charAt(0):''
 if(loading) return <Spinner/>;
 if(error) return <h4>Something Went Wrong!</h4>
const checkAdmin = user? user.role: '';
const isAdmin = checkAdmin==='authorizedAdmin'
  return (
    <>
      {!loading && !error && (
       <div className="container d-flex justify-content-center align-content-center mx-auto w-75 card p-5 shadow-lg rounded-5" style={{height: '600px'}}>
        <Link to={isAdmin?'/adminDashboard':'/userDashboard'} className='btn btn-light btn-sm w-25 d-inline ms-auto mt-2'>Back</Link>
        {data.user.fileSystem?<img className="avatar profile-nav avatar-16 bg-light rounded-circle text-white" src={data.user.fileSystem} alt={data.user.name}/>:
        <span class="avatar profile-text text-nav avatar-26  bg-success text-white rounded-circle d-flex justify-content-center align-items-center" style={{width: '10rem', height: '10rem', fontSize:'5rem'}}>{userName}</span> }

        <h1 style={{margin: 'auto'}}>Name: </h1>
        <h5 style={{margin: 'auto'}}>{data.user.name}</h5>
        <h1 className="mt-2" style={{margin: 'auto'}}>Email</h1>
        <h5 style={{margin: 'auto'}}>{data.user.email}</h5>
        <h1 className="mt-2" style={{margin: 'auto'}}>Role</h1>
        <h5 style={{margin: 'auto'}}>{data.user.role}</h5>




       </div>
      )}
      <EditProfileForm user={data.user}/>
      <DeleteUserButton id={data.user.id} user={user} setUser={setUser}/>
    </>
  )
}

export default Profile
