import React, {useState, useEffect} from 'react'
import {NavLink} from 'react-router-dom'
import {FaChartBar} from 'react-icons/fa'
import {FaTasks} from 'react-icons/fa'
import {HiUsers} from 'react-icons/hi'
import {FaHandPointRight} from 'react-icons/fa'
import {GiTeamIdea} from 'react-icons/gi'
import {CgProfile} from 'react-icons/cg'

import './Sidebar.css'
const Sidebar = ({showSidebar, setShowSidebar, user}) => {
     const [size, setSize] = useState(window.innerWidth) 

    const adminCheck = user?user.role: '';
    const isAdmin = adminCheck === 'authorizedAdmin';

     const checkSize = () => {
     setSize(window.innerWidth)
 }

  useEffect(()=>{
   window.addEventListener('resize', checkSize)
   return ()=> window.removeEventListener('resize', checkSize)
 })


  return (
    
        <nav id="sidebar" className={`${!showSidebar? 'active': ''} `} >
            {isAdmin && <ul className="list-unstyled components">
                <h5 >ADMIN TABS</h5>
                
                <li>
                    <NavLink to='/adminDashboard' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                    <div className='d-flex align-items-center'>
                     <FaTasks className='icon' style={{color: 'blueviolet'}} />
                     <div>All Tasks</div>
                    </div>

                    </NavLink>
                </li>
                <li>
                    <NavLink to='/allUsers' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                        <div className="d-flex align-items-center">
                            <HiUsers className='icon' style={{color: 'darkgray'}} />
                            <div>All Users</div>
                        </div>
                    </NavLink>
                </li>

                <li>
                    <NavLink to='/allTeamMembers' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                        <div className="d-flex align-items-center">
                            <GiTeamIdea className='icon' style={{color: 'darkblue'}} />
                            <div>All Team Members</div>
                        </div>
                    </NavLink>
                </li>

                            
                <li>
                    <NavLink to='/allStats' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                        <div className="d-flex align-items-center">
                            <FaChartBar className='icon' style={{color: 'darkgreen'}} />
                            <div>Stats</div>
                        </div>

                    </NavLink>
                </li>
                <li>
                    <NavLink to='/profile' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                        <div className="d-flex align-items-center">
                            <CgProfile className='icon' style={{color: 'darkred'}} />
                            <div>Profile</div>
                        </div>

                    </NavLink>

                </li>
            </ul>}
            {!isAdmin && (
                <ul className="list-unstyled components">
                    <h5>USER TABS</h5>
                <li>
                    <NavLink to='userStats' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                        <div className="d-flex align-items-center">
                            <FaChartBar className='icon' style={{color: 'darkgreen'}} />
                            <div>Stats</div>
                        </div>

                    </NavLink>
                </li>
                <li>
                    <NavLink to='/userDashBoard' onClick={()=>{if(size<960) setShowSidebar(true)}}> 
                    <div className='d-flex align-items-center'>
                     <FaTasks className='icon' style={{color: 'blueviolet'}} />
                     <div>Your Tasks</div>
                    </div>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='/userTeamMembers' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                        <div className="d-flex align-items-center">
                            <GiTeamIdea className='icon' style={{color: 'darkblue'}} />
                            <div>Your Team</div>
                        </div>
                    </NavLink>
                </li>
                <li>
                    <NavLink to='/assignedToYou' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                        <div className="d-flex align-items-center">
                            <FaHandPointRight className='icon' style={{color: 'darkmagenta'}} />
                            <div>Assigned To You</div>
                        </div>

                    </NavLink>
                </li>

                <li>
                    <NavLink to='/profile' onClick={()=>{if(size<960) setShowSidebar(true)}}>
                        <div className="d-flex align-items-center">
                            <CgProfile className='icon' style={{color: 'darkred'}} />
                            <div>Profile</div>
                        </div>

                    </NavLink>
                </li>
            </ul>
            )}

        </nav>

 
  )
}

export default Sidebar
