import React from 'react'
import {Link, useParams} from 'react-router-dom'
import Spinner from '../components/Spinner'
import { useQuery } from '@apollo/client'
import { GET_TASK } from '../queries/taskQueries'
import TeamMemberInfo from '../components/TeamMemberInfo'
import CreatorInfo from '../components/CreatorInfo'
import DeleteTaskButton from '../components/DeleteTaskButton'
import EditTaskForm from '../components/EditTaskForm'

const Task = ({user,}) => {
 const {id} = useParams();
 const{loading, error, data} = useQuery(GET_TASK, {
  variables: {
   id: id
  }
 })
 if(loading) return <Spinner/>;
 if(error) return <h4>Something Went Wrong!</h4>
const checkAdmin = user? user.role: '';
const isAdmin = checkAdmin==='authorizedAdmin'
  return (
    <>
      {!loading && !error && (
       <div className="mx-auto w-75 card p-5">
        <Link to={isAdmin?'/adminDashboard':'/userDashboard'} className='btn btn-light btn-sm w-25 d-inline ms-auto mt-5'>Back</Link>
        <h1>Title: </h1>
        <h5>{data.task.title}</h5>
        <h5 className="mt-5">Description:</h5>
        <p style={{fontWeight: '600', color: "teal"}}>{data.task.description}</p>
        <h5 className="mt-5">Created At:</h5>
        <p style={{fontWeight: '600', color: "teal"}}>{data.task.createdAt}</p>
        <h5 className="mt-5">Updated At:</h5>
        <p style={{fontWeight: '600', color: "teal"}}>{data.task.updatedAt}</p>
        <h5 className="mt-5">Priority:</h5>
        <p style={{fontWeight: '600', color: "teal"}}>{data.task.priority}</p>
        <h5 className="mt-3">Project Status:</h5>
        <p style={{fontWeight: '600', color: "teal"}}>{data.task.status}</p>
        <h5 className="mt-3">System:
        </h5>
        <p style={{fontWeight: '600', color: "teal"}}>{data.task.system}</p>
        <h5 className="mt-3">Working Hours To Complete:
        </h5>
        <p style={{fontWeight: '600', color: "teal"}}>{data.task.workingHoursToComplete} h</p>
        <h5 className="mt-3">Image
        </h5>
        <img src={data.task.fileSystem} alt={data.task.title} className='img-task'/>



       <CreatorInfo creator={data.task.creator}/>
       <TeamMemberInfo teamMember={data.task.assignedTo}/>
       <EditTaskForm task={data.task} user={user}/>
       <DeleteTaskButton task={data.task} taskId={data.task.id} user={user}/>
       </div>
      )}
    </>
  )
}

export default Task
