import {gql} from "@apollo/client";

const ADD_TEAM_MEMBER = gql`
mutation addTeamMember($creatorId: ID!, $name: String!, $email: String!, $phone: String!, $role: String!,) {
  addTeamMember(creatorId: $creatorId, name: $name, email: $email, phone: $phone, role: $role, ){
    id,
    name,
    email,
    phone,
    role,
  }
}
`


const DELETE_TEAM_MEMBER = gql`
  mutation deleteTeamMember($id: ID!) {
    deleteTeamMember(id: $id) {
      id
      name
      email
      phone
      role
    }
  }
`;






export {DELETE_TEAM_MEMBER, ADD_TEAM_MEMBER};