import { gql } from "@apollo/client";

const ADD_TASK = gql`
mutation addTask($creatorId: ID!, $teamMemberId: ID!, $title: String!, $description: String!, $status: String!, $priority: String!, $system: String!, $createdAt: String!, $workingHoursToComplete: String!, $fileSystem: String, $fileName: String){
 addTask(creatorId: $creatorId, teamMemberId: $teamMemberId, title: $title, description: $description, status: $status, priority: $priority, system: $system, createdAt: $createdAt, workingHoursToComplete: $workingHoursToComplete, fileSystem: $fileSystem, fileName: $fileName){
   id
   title
   priority
   status
   system

 }
}
`

const DELETE_TASK = gql`
mutation deleteTask($id: ID){
 deleteTask(id: $id){
  id
 }
}
`

const UPDATE_TASK = gql`
mutation updateTask($id: ID!, $teamMemberId: ID!, $title: String!, $description: String!, $status: String!, $priority: String!, $system: String!, $updatedAt: String!, $workingHoursToComplete: String!, $fileSystem: String, $fileName: String){
 updateTask(id: $id, teamMemberId: $teamMemberId, title: $title, description: $description, status: $status, priority: $priority, system: $system, updatedAt: $updatedAt, workingHoursToComplete: $workingHoursToComplete, fileSystem: $fileSystem, fileName: $fileName){
   id
   title
   priority
   status
   system

 }
}
`



export {ADD_TASK, DELETE_TASK, UPDATE_TASK}