import { gql } from "@apollo/client";


const GET_TASKS = gql`
 query getTasks {
  tasks {
  id
  creatorId
  title
  priority
  status
  system
  createdAt
  updatedAt
  assignedTo{
   name
  }
  }
 }
`
const GET_TASK = gql`
query getTask($id: ID!){
 task(id: $id){
  id
  title 
  priority
  status
  system
  description
  workingHoursToComplete
  fileSystem
  fileName
  creatorId
  creator{
   id
   name
   email
   role
  }
  assignedTo{
   id
   name
   email
   phone 
   role
  }
  createdAt
  updatedAt
 }
}
`


export {GET_TASKS, GET_TASK, };