import React, {useState,useEffect} from 'react'
import axios from 'axios'
import {useMutation } from '@apollo/client';
import { UPDATE_USER } from '../mutations/user';
import {AiFillFileImage} from 'react-icons/ai'
import { GET_USER } from '../queries/user';

const EditProfileForm = ({user, setUser}) => {
  const[name, setName] = useState(user?user.name:'')
  const[email, setEmail] = useState(user?user.email:'');
  const[password, setPassword]= useState('')
  const[role, setRole] = useState(user?user.role:'');
  const[file, setFile] = useState('')
  const[fileSystem, setFileSystem]=useState('')
  const[fileName, setFileName]=useState('')
  const[previousFileName, setPreviousFileName]=useState(user?user.fileName:'')
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');


const id = user?user.id:'';
  const [updateUser] = useMutation(UPDATE_USER, {
    variables: {id, name, email, password, role, fileSystem, fileName },
   async update(cache, { data: { updateUser } }) {
     localStorage.setItem('registerUser', JSON.stringify(updateUser))
     localStorage.setItem('loginUser', JSON.stringify(updateUser))
    try {
      const result = await updateUser;
      if(result.name){
        setUser(result);
      }else{
        setErrorMessage('User already exists')
      }
    } catch (error) {
      console.log(error);

    }

    },
  refetchQueries: [{ query: GET_USER, variables: { id: id } }],
  });
  
 const deleteUserImage = async (file) =>{
  try {
    
    if(!file) {return}
    else{
      const {data}= await axios.delete(`/delete/${file}`)
      console.log(data);
    }
  } catch (error) {
    console.log(error);
  }
 }



useEffect(()=>{
  if(errorMessage){

    const timeout1 = setTimeout(()=>setErrorMessage('User already exists'),100);
  
    const timeout2 = setTimeout(()=>setErrorMessage(''), 2000);
    return ()=> clearTimeout(timeout1, timeout2)
  }
}, [errorMessage,])

 async function postImage(image) {
  const formData = new FormData();
  formData.append("file", image)

  const result = await axios.post('/upload', formData, { headers: {'Content-Type': 'multipart/form-data'}})
  return result.data
}

  const uploadImage = async (e) => {
    e.preventDefault();
    setLoading(true)
    const result = await postImage(file)
    setFileSystem(result.location)
    setFileName(result.name)
    setLoading(false)
  }
  

  const onSubmit = async (e) => {
    e.preventDefault();
    if (name === '' || email === '' || password === '' || role === '')  {
      return alert('Please fill in all fields');
    }
    

   if(fileSystem){
     updateUser(id,name, email, password, role, fileSystem, fileName);
     deleteUserImage(previousFileName)
   }else{
    updateUser(id,name, email, password, role);
   }
   
    setName('');
    setEmail('');
    setPassword('');
    setRole('');
    setFileSystem('')
    setFileName('')
    setFile('')
    setPreviousFileName('')

  };

  return (
            <div >
             <div className='container mt-5 col-md-5'>
              <h3>Update Profile</h3>
               <form>
                  <div className='mb-3'>
                  <label className='form-label'>Avatar Image (Optional)</label>
                  <input
                    type='file'
                    className='form-control'
                    id='file'
                    
                    onChange={(e) => setFile(e.target.files[0])}
                  />
                </div>
                
                <h4 className='mb-3'>{loading?'Uploading...': !loading&&fileSystem?<AiFillFileImage style={{fontSize: '50px', color: 'magenta'}}/>:''}</h4>
                
                <button
                  type='submit'
                  className='btn btn-secondary mb-3'
                 onClick={ uploadImage}>Upload</button>



              </form>
              <form onSubmit={onSubmit}>
              

               {errorMessage &&  <h3>{errorMessage}</h3>}
                 <div className='mb-3'>
                  <label className='form-label'>Name</label>
                  <input
                    type='text'
                    className='form-control'
                    id='name'
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                </div>
                <div className='mb-3'>
                  <label className='form-label'>Email</label>
                  <input
                    type='email'
                    className='form-control'
                    id='email'
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </div>
                <div className='mb-3'>
                  <label className='form-label'>Password</label>
                  <input
                    type='password'
                    className='form-control'
                    id='password'
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </div>




                <div className='mb-3'>
                  <label className='form-label'>Role</label>
                  <input
                    type='text'
                    className='form-control'
                    id='role'
                    value={role}
                    onChange={(e) => setRole(e.target.value)}
                  />
                </div>


                <button
                  type='submit'
                  className='btn btn-secondary'
                >
                  Update Profile
                </button>
                
              </form>
            </div>
           </div>

  )
}

export default EditProfileForm
