import React  from 'react'
import { GET_TASKS } from '../queries/taskQueries'
import {FETCH_USERS} from '../queries/user'
import { GET_TEAM_MEMBERS } from '../queries/teamMemberQueries'
import { useQuery } from '@apollo/client'
import {MdDirectionsRun} from 'react-icons/md'
import {MdBrightnessMedium} from 'react-icons/md'
import {MdBrightnessLow} from 'react-icons/md'
import {MdCallToAction} from 'react-icons/md'
import {MdAssignmentTurnedIn} from 'react-icons/md'
import {SiMicrosoftteams} from 'react-icons/si'
import {GiTeamUpgrade} from 'react-icons/gi'
import {FaTasks} from 'react-icons/fa'
import Stat from '../components/Stat'
import BarChart from '../components/BarChart'


const AdminStatistics = () => {



  const getTasks = () => {
    const{data}= useQuery(GET_TASKS)
    const tasks = data?data.tasks:[]
    return tasks;
  }

  const getUsers  = () => {
    const{data}= useQuery(FETCH_USERS)
    const users = data?data.users:[]
    return users
  }

  const getTeamMembers  = () => {
    const{data}= useQuery(GET_TEAM_MEMBERS)
    const teamMembers = data?data.teamMembers:[]
    return teamMembers
  }
 const tasks = getTasks();
 const users = getUsers();
 const teamMembers = getTeamMembers();


 const pendingTasks = tasks.filter(task=>task.status === 'Pending').length

 const completedTasks = tasks.filter(task=>task.status === 'Completed').length
 const priorityOne = tasks.filter(task=>task.priority === '#1').length
 const priorityTwo = tasks.filter(task=>task.priority === '#2').length
 const priorityThree = tasks.filter(task=>task.priority === '#r').length

 const totalTasks = tasks.length

 const totalUsers = users.length

 const totalTeamMembers = teamMembers.length

 let monthlyCreated;
let monthlyCompleted;


const monthsPending = tasks.filter(task=>task.status === 'Pending')
const monthCreatedPending = monthsPending.map(task=>{
  return task.createdAt})
const monthsCompleted = tasks.filter(task=>task.status === 'Completed')
const monthCompleted = monthsCompleted.map(task=>{
  return task.updatedAt || task.createdAt})
  const currentYear = new Date().getFullYear()

const createdInJanuary = monthCreatedPending.filter(task=>task.includes(`${currentYear}-01`)||task.includes(`${currentYear-1}-01`)).length
const createdInFebruary = monthCreatedPending.filter(task=>task.includes(`${currentYear}-02`)||task.includes(`${currentYear-1}-02`)).length
const createdInMarch = monthCreatedPending.filter(task=>task.includes(`${currentYear}-03`)||task.includes(`${currentYear-1}-03`)).length
const createdInApril = monthCreatedPending.filter(task=>task.includes(`${currentYear}-04`)||task.includes(`${currentYear-1}-04`)).length
const createdInMay = monthCreatedPending.filter(task=>task.includes(`${currentYear}-05`)||task.includes(`${currentYear-1}-05`)).length
const createdInJune = monthCreatedPending.filter(task=>task.includes(`${currentYear}-06`)||task.includes(`${currentYear-1}-06`)).length
const createdInJuly = monthCreatedPending.filter(task=>task.includes(`${currentYear}-07`)||task.includes(`${currentYear-1}-07`)).length
const createdInAugust = monthCreatedPending.filter(task=>task.includes(`${currentYear}-08`)||task.includes(`${currentYear-1}-08`)).length
const createdInSeptember = monthCreatedPending.filter(task=>task.includes(`${currentYear}-09`)||task.includes(`${currentYear-1}-09`)).length
const createdInOctober = monthCreatedPending.filter(task=>task.includes(`${currentYear}-10`)||task.includes(`${currentYear-1}-10`)).length
const createdInNovember = monthCreatedPending.filter(task=>task.includes(`${currentYear}-11`)||task.includes(`${currentYear-1}-11`)).length
const createdInDecember = monthCreatedPending.filter(task=>task.includes(`${currentYear}-12`)||task.includes(`${currentYear-1}-12`)).length

monthlyCreated=[createdInJanuary, createdInFebruary, createdInMarch, createdInApril, createdInMay, createdInJune, createdInJuly, createdInAugust, createdInSeptember, createdInOctober, createdInNovember, createdInDecember]





const completedInJanuary = monthCompleted.filter(task=>task.includes(`${currentYear}-01`)||task.includes(`${currentYear-1}-01`)).length
const completedInFebruary = monthCompleted.filter(task=>task.includes(`${currentYear}-02`)||task.includes(`${currentYear-1}-02`)).length
const completedInMarch = monthCompleted.filter(task=>task.includes(`${currentYear}-03`)||task.includes(`${currentYear-1}-03`)).length
const completedInApril = monthCompleted.filter(task=>task.includes(`${currentYear}-04`)||task.includes(`${currentYear-1}-04`)).length
const completedInMay = monthCompleted.filter(task=>task.includes(`${currentYear}-05`)||task.includes(`${currentYear-1}-05`)).length
const completedInJune = monthCompleted.filter(task=>task.includes(`${currentYear}-06`)||task.includes(`${currentYear-1}-06`)).length
const completedInJuly = monthCompleted.filter(task=>task.includes(`${currentYear}-07`)||task.includes(`${currentYear-1}-07`)).length
const completedInAugust = monthCompleted.filter(task=>task.includes(`${currentYear}-08`)||task.includes(`${currentYear-1}-08`)).length
const completedInSeptember = monthCompleted.filter(task=>task.includes(`${currentYear}-09`)||task.includes(`${currentYear-1}-09`)).length
const completedInOctober = monthCompleted.filter(task=>task.includes(`${currentYear}-10`)||task.includes(`${currentYear-1}-10`)).length
const completedInNovember = monthCompleted.filter(task=>task.includes(`${currentYear}-11`)||task.includes(`${currentYear-1}-11`)).length
const completedInDecember = monthCompleted.filter(task=>task.includes(`${currentYear}-12`)||task.includes(`${currentYear-1}-12`)).length

monthlyCompleted=[completedInJanuary, completedInFebruary, completedInMarch, completedInApril, completedInMay, completedInJune, completedInJuly, completedInAugust, completedInSeptember, completedInOctober, completedInNovember, completedInDecember]

let months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']


const currentMonth = new Date().getMonth() + 1;

const recentMonths = months.slice(0, currentMonth)
const earlierMonths = months.slice(currentMonth, months.length)
months = [...earlierMonths, ...recentMonths]


const recentCreated = monthlyCreated.slice(0, currentMonth);
const earlierCreated = monthlyCreated.slice(currentMonth, monthlyCreated.length)

monthlyCreated = [...earlierCreated, ...recentCreated]

const recentCompleted = monthlyCompleted.slice(0, currentMonth);
const earlierCompleted = monthlyCompleted.slice(currentMonth, monthlyCompleted.length)

monthlyCompleted = [...earlierCompleted, ...recentCompleted]


 const stats = [
  {title: 'Priority One', amount: priorityOne, color: 'teal', icon: <MdDirectionsRun style={{color:'teal'}}/>}, 
  {title: 'Priority Two', amount: priorityTwo, color: 'darkred', icon: <MdBrightnessMedium style={{color:'darkred'}}/>},
  {title: 'Priority Three', amount: priorityThree, color: 'purple', icon: <MdBrightnessLow style={{color:'purple', opacity: '0.6'}}/>},
  {title: 'Pending Tasks', amount: pendingTasks,color: 'magenta', icon: <MdCallToAction style={{color:'magenta'}}/>}, 
  { title: 'Completed Tasks', amount: completedTasks, color: 'green', icon: <MdAssignmentTurnedIn style={{color:'green'}}/>}, 
  { title: 'Total Tasks', amount: totalTasks, color: 'crimson', icon: <FaTasks style={{color:'crimson'}}/>}, 
  { title: 'Total Users', amount: totalUsers, color: 'goldenrod', icon: <GiTeamUpgrade style={{color:'goldenrod'}}/>}, 
  { title: 'Total Team Members', amount: totalTeamMembers, color: 'black', icon: <SiMicrosoftteams style={{color:'crimson'}}/>}, 
 ]



  return (
    <>
    <div className='container mt-4'>
        <div className="col-sm d-flex justify-content-center mb-4 text-success">
          <h3>Stats</h3>
        </div>
        <div className="container stat">

       {stats.map((stat, index)=>{
        return <Stat key={index} stat={stat}/>
       })}
        </div>
       </div>
       <br/>
      <div className='d-flex justify-content-center align-items-center mt-5 bg-inherit chart'>
        <BarChart chartData={{labels: months,
        datasets:[{ 
          label: 'Created In last 12 Months',data:monthlyCreated,
          backgroundColor:["darkred",],
        }],
        
      }}/>
        <BarChart chartData={{labels: months,
        datasets:[{ 
          label: 'Completed In Last 12 Months',
          data: monthlyCompleted,
          backgroundColor:["green",]
        }],
        
      }}/>
      </div>

    </>
  )
}

export default AdminStatistics
