import React from 'react'
import {FaTrash} from "react-icons/fa"
import {DELETE_TEAM_MEMBER} from '../mutations/teamMemberMutations';
import { useMutation } from '@apollo/client';
import { GET_TEAM_MEMBERS } from '../queries/teamMemberQueries';
import { GET_USER } from '../queries/user';

const TeamMemberRow = ({teamMember, user}) => {
  const [deleteTeamMember] = useMutation(DELETE_TEAM_MEMBER, {
    variables: {id: teamMember.id},
    // refetchQueries: [{query: GET_TEAM_MEMBERS}]
    update(cache, {data: { deleteTeamMember}}) {
      const {teamMembers} = cache.readQuery({query: GET_TEAM_MEMBERS});
      cache.writeQuery({
        query: GET_TEAM_MEMBERS,
        data: {teamMembers: teamMembers.filter(teamMember => teamMember.id!== deleteTeamMember.id)}
      })
    },
    refetchQueries: [{ query: GET_USER, variables: { id: user?user.id: '' } }],
  })
  
  return (
    <tr>
      <td>{teamMember.name}</td>
      <td>{teamMember.email}</td>
      <td>{teamMember.phone}</td>
      <td>{teamMember.role}</td>
      <td>
       <button className='btn btn-danger btn-sm' onClick={deleteTeamMember}>
        <FaTrash/>
       </button>
      </td>
    </tr>
  )
}

export default TeamMemberRow
