import React from 'react'
import { Link } from 'react-router-dom'

const TaskCard = ({task,}) => {
  return (
    <div className='col-md-3'>
      <div className="card mb-3 p-2 shadow-sm rounded" >
       <div className="d-flex justify-content-between gap-5 align-items-center " >
        <h5 className='card-title' style={{color: 'rgb(4, 4, 43)'}}>Title: </h5>
        <p className='card-title' style={{color: 'rgb(43, 124, 175)'}}>{task.title}</p>
       </div>
       <div className="d-flex justify-content-between gap-5 align-items-center">
        <h5 className='card-title' style={{color: 'darkred'}}>Priority:</h5>
        <p className='card-title' style={{color: 'magenta'}}>{task.priority}</p>
       </div>
        <div className="d-flex justify-content-between gap-5 align-items-center">
        <h5 className='card-title' style={{color: 'purple'}}>Status: </h5>
        <p className='card-title ' style={{color: 'red'}}>{task.status}</p>
       </div>
       <div className="d-flex justify-content-between  gap-5 align-items-center">
        <p className='card-title' style={{color: 'blue'}}>System: </p>
        <p className='card-title ' style={{color: 'black', fontSize: '14px'}}>{task.system}</p>
       </div>

       <hr />

        <Link className='btn btn-light ' to ={`/tasks/${task.id}`}>View Task</Link>


      </div>
    </div>
  )
}

export default TaskCard
