import React, {useEffect, } from "react"
import {AiOutlineMenuFold} from 'react-icons/ai'
import { useMutation, useQuery } from "@apollo/client"
import './Sidebar.css'
import { LOGOUT } from "../mutations/logout";

import { GET_USER, } from '../queries/user'

import { useNavigate, Link } from "react-router-dom";
const Header = ({user, setUser, showSidebar,setShowSidebar}) => {
  const navigate = useNavigate()

 useEffect(()=>{
  const newUser = JSON.parse(localStorage.getItem('user'))
  if(newUser){
    setUser(newUser)
  }
 },[])

 
 const [logout] = useMutation(LOGOUT, {
   variables:{id: user?user.id: null},
   
  })
  
  
  const handleLogout = () => {
    localStorage.clear();
    logout(user? user.id: null);
    setUser(null);
    navigate('/');
  }

  useEffect(()=>{


    const timeout = setTimeout(()=>handleLogout(), 3600 * 1000);
  
    return ()=> clearTimeout(timeout)

}, [])

 const id = user?user.id:'';
 const{data} = useQuery(GET_USER, {
  variables: {
   id: id
  }
 })
 if(data){
  setUser(data.user)
 }
const userImage = data?data.user.fileSystem : null;
const userName = data?data.user.name.charAt(0): '';

  return (
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="container-fluid">

                    <button type="button" id="sidebarCollapse" className="btn " onClick={()=>setShowSidebar(!showSidebar)}>
                        <AiOutlineMenuFold className="toggle-icon"/>
                    </button>
                    

                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                      <h2><span>Easy</span>Tasking</h2>
                    </div>
                    <div className="collapse navbar-collapse" >
                      <h2 style={{color: 'rgb(29, 8, 78)'}}>DASHBOARD</h2>
                    </div>
                    <div className="profile">
                      {user && <h4>{user.name}</h4>}
                      {userImage?<img className="avatar avatar-nav avatar-16 bg-light rounded-circle text-white" src={userImage} alt={user.name}/>:
                      <span className="avatar text-nav avatar-16  bg-success text-white rounded-circle">{userName}</span> }
                    </div>
                 {user?<button className="btn btn-primary" 
                 onClick={handleLogout}>
                       Logout
                    </button>:
                     <button className="btn btn-secondary" >
                      <Link to='/auth'>Login</Link>
                    </button> }
                </div>
            </nav>
  )
}

export default Header
