import React, {useState,useEffect} from 'react'
import axios from 'axios'
import {useMutation } from '@apollo/client';
import { ADD_USER } from '../mutations/user';
import {useNavigate} from 'react-router-dom';
import {LOGIN} from '../mutations/login'
import {AiFillFileImage} from 'react-icons/ai'

const Auth = ({user, setUser}) => {
  const[name, setName] = useState('')
  const[email, setEmail] = useState('');
  const[password, setPassword]= useState('')
  const[role, setRole] = useState('');
  const[file, setFile] = useState('')
  const[fileSystem, setFileSystem]=useState('')
  const[fileName, setFileName]=useState('')
  const[isLogin, setIsLogin] = useState(false);
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const navigate = useNavigate();

  const [login, {error}] = useMutation(LOGIN, {
    variables: { email, password},
    async update(cache, { data: { login } }) {
    localStorage.setItem('user', JSON.stringify(login))


    try {
      const result = await login;
      if(result){
        setUser(result);
      }
    } catch (error) {
     console.log(error);
    }
    },
 
  });



  const [addUser] = useMutation(ADD_USER, {
    variables: { name, email, password, role, fileSystem, fileName },
   async update(cache, { data: { addUser } }) {
     localStorage.setItem('user', JSON.stringify(addUser))
    try {
      const result = await addUser;
      if(result.name){
        setUser(result);
      }else{
        setErrorMessage('User already exists')
      }
    } catch (error) {
      console.log(error);

    }

    },
 
  });
  

if(user){
  if(user.role === 'authorizedAdmin'){
    navigate('/adminDashboard')
  }else{
    navigate('/userDashboard')
  }
}

useEffect(()=>{
  if(error && ! isLogin){

    const timeout1 = setTimeout(()=>setErrorMessage('Wrong Credentials'),100);
  
    const timeout2 = setTimeout(()=>setErrorMessage(''), 2500);
    return ()=> clearTimeout(timeout1, timeout2)
  }
}, [error, !isLogin])

useEffect(()=>{
  if(errorMessage && isLogin){

    const timeout1 = setTimeout(()=>setErrorMessage('User already exists'),100);
  
    const timeout2 = setTimeout(()=>setErrorMessage(''), 2000);
    return ()=> clearTimeout(timeout1, timeout2)
  }
}, [errorMessage, isLogin])

 async function postImage(image) {
  const formData = new FormData();
  formData.append("file", image)

  const result = await axios.post('/upload', formData, { headers: {'Content-Type': 'multipart/form-data'}})
  return result.data
}

  const uploadImage = async (e) => {
    e.preventDefault();
    setLoading(true)
    const result = await postImage(file)
    setFileSystem(result.location)
    setFileName(result.name)
    setLoading(false)
  }

  const onSubmit = async (e) => {
    e.preventDefault();
    if ((name === '' || email === '' || password === '', role === '') && isLogin) {
      return alert('Please fill in all fields');
    }
    if ((email === '' || password === '') && !isLogin) {
      return alert('Please fill in all fields');
    }
    

    if(isLogin&&fileSystem){
    addUser(name, email, password, role, fileSystem, fileName);
    }
    if(isLogin&&!fileSystem){
    addUser(name, email, password, role);
    }



    if(!isLogin){
      
      login(email, password);
    }





    setName('');
    setEmail('');
    setPassword('');
    setRole('');
    setFileSystem('')
    setFileName('')
    setFile('')
  };

  const switchMode = () =>{
    setIsLogin(!isLogin)
    setErrorMessage('')
  }

  return (
            <div className="auth-wrapper">
             <div className='container mt-5 col-md-5'>
              <h3>{isLogin?'Register':'Login'}</h3>
               {isLogin &&<form>
                  <div className='mb-3'>
                  <label className='form-label'>Avatar Image (Optional)</label>
                  <input
                    type='file'
                    className='form-control'
                    id='file'
                    
                    onChange={(e) => setFile(e.target.files[0])}
                  />
                </div>
                
                <h4 className='mb-3'>{loading?'Uploading...': !loading&&fileSystem?<AiFillFileImage style={{fontSize: '50px', color: 'magenta'}}/>:''}</h4>
                
                <button
                  type='submit'
                  data-bs-dismiss='modal'
                  className='btn btn-secondary mb-3'
                 onClick={ uploadImage}>Upload</button>



              </form>}
              <form onSubmit={onSubmit}>
              

               {errorMessage && !isLogin && <h3>{errorMessage}</h3>}
               {errorMessage && isLogin && <h3>{errorMessage}</h3>}
                {isLogin && <div className='mb-3'>
                  <label className='form-label'>Name</label>
                  <input
                    type='text'
                    className='form-control'
                    id='name'
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                </div>}
                <div className='mb-3'>
                  <label className='form-label'>Email</label>
                  <input
                    type='email'
                    className='form-control'
                    id='email'
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </div>
                <div className='mb-3'>
                  <label className='form-label'>Password</label>
                  <input
                    type='password'
                    className='form-control'
                    id='password'
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </div>




                {isLogin && <div className='mb-3'>
                  <label className='form-label'>Role</label>
                  <input
                    type='text'
                    className='form-control'
                    id='role'
                    value={role}
                    onChange={(e) => setRole(e.target.value)}
                  />
                </div>}


                <button
                  type='submit'
                  data-bs-dismiss='modal'
                  className='btn btn-secondary'
                >
                  {isLogin?'Register':'Login'}
                </button>
                
                <button type='button'  className='switch-mode' onClick={switchMode}>{isLogin?'Already have an account? Login':"Don't have an account? Register"}</button>
              </form>
            </div>
           </div>

  )
}

export default Auth
