import React from 'react'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { FaTrash } from 'react-icons/fa'
import { GET_TASKS, GET_TASK } from '../queries/taskQueries'
import { useMutation, useQuery } from '@apollo/client'
import { DELETE_TASK } from '../mutations/taskMutations'

const DeleteTaskButton = ({taskId, user,}) => {
 const navigate = useNavigate();
 const checkAdmin = user? user.role: '';
 const isAdmin =checkAdmin === 'authorizedAdmin'
 
 const{data}=useQuery(GET_TASK, {
  variables:{id: taskId}
 })
 const [deleteTask] = useMutation(DELETE_TASK, {
  variables: {id: taskId},
  onCompleted: ()=> isAdmin? navigate('/adminDashboard'): navigate('/userDashboard'),
  refetchQueries: [{query: GET_TASKS}]
 })

 const deleteTaskImage = async (file) =>{
  try {
    
    if(!file) {return}
    else{
      await axios.delete(`/delete/${file}`)
      
    }
  } catch (error) {
    console.log(error);
  }
 }

 const handleDelete = () =>{
  deleteTask(taskId)
  deleteTaskImage(data.task.fileName)
 }
  return (
    <div className='d-flex mt-5 ms-auto'>
     <button className="btn btn-danger m-2 d-flex gap-1 align-items-center" onClick={handleDelete}>
      <FaTrash className=''/>Delete Task
     </button>
      
    </div>
  )
}

export default DeleteTaskButton
