import React from 'react'
import './Stat.css'


const Stat = ({stat}) => {
  return (
     <div className='card row' style={{borderBottom: `5px solid ${stat.color}`}}>
      <div>
        <div className='info col'>
          <div>{stat.icon}</div>
          <h4> {stat.amount}</h4>
        </div>
      <h6>{stat.title}</h6>
      </div>
    </div>
   
  )
}

export default Stat
