import React from 'react'
import { useNavigate } from 'react-router-dom'
import { FaTrash } from 'react-icons/fa'
import { useMutation, useQuery } from '@apollo/client'
import { DELETE_USER } from '../mutations/user'
import {GET_USER} from '../queries/user'
import { LOGOUT } from '../mutations/logout'
import axios from 'axios'

const DeleteUserButton = ({id, user, setUser}) => {
 const navigate = useNavigate();
 const {data} = useQuery(GET_USER, {
  variables:{
    id: id
  }
 })
 const [deleteUser] = useMutation(DELETE_USER, {
  variables: {id: id},
  onCompleted: ()=> navigate('/'),
 })
  const [logout] = useMutation(LOGOUT, {
   variables:{id: id},
   
  })
  
 const deleteUserImage = async (file) =>{
  try {
    
    if(!file) {return}
    else{
      const {data}= await axios.delete(`/delete/${file}`)
      console.log(data);
    }
  } catch (error) {
    console.log(error);
  }
 }
  
  const handleDelete = () => {
    localStorage.clear();
    logout(id);
    deleteUser(id);
    deleteUserImage(data.user.fileName);
    setUser(null);
    navigate('/');
  }

  return (
    <div className='d-flex mt-5 ms-auto'>
     <button className="btn btn-danger m-2 d-flex gap-1 align-items-center" onClick={handleDelete}>
      <FaTrash className=''/>Unsubscribe
     </button>      
    </div>
  )
}

export default DeleteUserButton
